import React from "react";
import { Link } from "gatsby";
import { Box } from "jsxstyle";

const LinkButton = props => {
  let { text, link, internal } = props;

  const wordSpacing = 0.8;
  const pixels = text.length * wordSpacing;

  var innerHTML = (
    <Box
      component="button"
      border="1px solid rgba(0, 0, 0, 0.4)"
      color="rgba(0, 0, 0, 0.7)"
      padding="8px"
      borderRadius="16px"
      padding="16px 24px 16px 24px"
      background="white"
      marginRight="4px"
      marginBottom="4px"
      cursor="pointer"
      fontSize="16px"
      fontStyle="italic"
      hoverPaddingLeft={24 - pixels / 2 + "px"}
      hoverPaddingRight={24 - pixels / 2 + "px"}
      hoverLetterSpacing={wordSpacing + "px"}
      hoverBackgroundColor="rgba(0, 0, 0, 0.03)"
      transitionDuration="250ms"
      transitionTimingFunction="ease-in-out"
    >
      {text}{" "}
      {internal ? (
        ""
      ) : (
        <Box
          opacity="0.6"
          marginLeft="4px"
          marginTop="-1px"
          marginBottom="-1px"
          width="16px"
          component="img"
          props={{ src: "../images/external.svg" }}
        />
      )}
    </Box>
  );

  return internal ? (
    <Link to={link} key={"link" + text + link} className="wrapper">
      {innerHTML}
    </Link>
  ) : (
    <a
      href={link}
      target="__blank"
      key={"link" + text + link}
      className="wrapper"
    >
      {innerHTML}
    </a>
  );
};

export default LinkButton;
